@mixin button {
  font-weight: bold;
  color: $blue;
  background-color: $white;
  border: 1px solid $blue;
  border-radius: 5px;
  cursor: pointer;
  &:hover {
    background-color: $blue;
    color: white;
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }
}

@mixin layout {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: space-evenly;
  justify-content: space-evenly;
  width: inherit;
}

@mixin input {
  margin-right: 10px;
  padding: 5px;
  height: 30px;
  background-color: $white;
  font-size: 1rem;
  font-family: arial, sans-serif;
  border: 1px solid $black;
  border-radius: 5px;
  cursor: pointer;
}

@mixin buttonHover {
  &:hover {
    background-color: $black;
    color: $white;
    -webkit-transition: 0.3s;
    transition: 0.3s;
  }
}
