.header {
  position: fixed;
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  flex-direction: column;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  align-items: center;
  width: 100vw;
  min-height: 90px;
  background-color: $yellow;
  h1 {
    font-size: 1.9rem;
    text-transform: uppercase;
    color: $blue;
  }
  .menuBtn {
    width: 45vw;
    margin: 8px 0 3px 0;
    font-size: 1.3rem;
    padding: 2px;
    @include button;
  }
}
